// @flow
import React, { Component } from 'react';
import { observer } from 'mobx-react';
import Line from "./Line"
import { teams } from "../lib/gameInfo"

@observer
export default class ActionLines extends Component {
    render() {
        const { width, height, units } = this.props
        return (
            <svg className="linesSvg" height={height} width={width}>
                {
                    units.map(unit =>
                        <Line
                            key={unit.id}
                            unit={unit}
                            offset={unit.team === teams.red ? 3 : -3}
                            color={unit.team.colour}
                        />
                    )
                }
            </svg>
        )
    }
}


