// @flow
import { observable } from 'mobx'

export default class FrameRateController {

    @observable fps=0

    constructor( millisecondsPerFrame, targetFps ) {
        this.millisecondsPerTick = millisecondsPerFrame
        this.targetFps = targetFps
        this.filterStrength = 30;
        this.lastLoop = new Date();
        this.thisLoop = new Date();
    }

    start(factor: number = 1):void {
        this.speedUpFactor = factor
        this.startTime = new Date().getTime()
        this.frameTime = 0
        this.lastLoop = new Date() 
    }

    millisecondsPerFrame(): number {
        return 1000.0 / this.targetFps
    }

    ticksPerFrame(): number {
        return Math.round((1000 / this.millisecondsPerTick) / this.targetFps * this.speedUpFactor)
    }

    // Automatically lower frame rate
    changedFrameRate():boolean {
        let adjusted: boolean = false;
        let currentTime = (new Date()).getTime() 
        if ((currentTime - this.startTime) > 5000) {
            if (this.targetFps > 33 && this.fps < 45) {
                this.targetFps = 33
                adjusted = true
            }
            if (!(this.targetFps > 20) && this.fps < 25) {
                this.targetFps = 20
                adjusted = true
            }
        }
        if (adjusted) this.startTime = new Date().getTime()
        return adjusted;
    }

    recordFrame() { 
        var thisFrameTime = (this.thisLoop=new Date()) - this.lastLoop;
        this.frameTime += (thisFrameTime - this.frameTime) / this.filterStrength;
        this.lastLoop = this.thisLoop;
        this.fps = (1000/this.frameTime).toFixed(1)
    }

}


