// @flow
import React, { Component } from 'react';
import PropTypes from "prop-types";
import { observer } from 'mobx-react';

import DistanceCircles from "./DistanceCircles"
import UnitView from "./UnitView"
import ActionLines from "./ActionLines"
import Board from "../lib/Board"
import CanvasBullets from "./CanvasBullets"

@observer
class BoardView extends Component {

    props: {
        board: Board,
        children: PropTypes.element.isRequired,
    }

    render() {
        const { children, board: { width, height, units, bullets, clickOnUnit } } = this.props
        return (
            [
                <div className="board" style={{ width: width, height: height }}>
                    {children}
                    <CanvasBullets ref="canvas" width={width} height={height} bullets={bullets} />
                    <ActionLines height={height} width={width} units={units} />
                    {units.map(unit => <DistanceCircles key={unit.id} unit={unit} />)}
                    {units.map(unit => <UnitView key={unit.id} unit={unit} clickOnUnit={clickOnUnit} />)}
                </div>
            ]
        )
    }
}


export default BoardView



